.page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  text-align: center;
  padding: 2rem;
}

.bio-text {
  max-width: 600px;
  line-height: 1.6;
  margin-top: 1rem;
}

.toggle-container {
  margin: 2rem 0;
}

.toggle-button {
  background: none;
  color: #333;
  border: none;
  padding: 0;
  font-size: 1rem;
  font-weight: normal;
  cursor: pointer;
  text-decoration: underline;
  transition: color 0.2s ease;
}

.toggle-button:hover {
  color: #666;
}

.detailed-bio {
  max-width: 600px;
  line-height: 1.6;
  margin-top: 1rem;
  text-align: center;
  animation: fadeIn 0.4s ease-out;
}

.detailed-bio p {
  line-height: 1.6;
  margin-bottom: 1rem;
}

.detailed-bio p:last-child {
  margin-bottom: 0;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
